<template>
    <div class="mb-2">
      <div class="left-menu-item text-white hand" @click="collapseMenu()">
        <div class="item-father">
          
          <slot name="headerLeading"></slot>
            
          <slot name="headerText"></slot>
          
          <slot name="headerAction"></slot>
          
        </div>
      </div>
      <div class="item-children text-white hand collapse" :id="collapseId">
        <slot name="slot_content"></slot>
      </div>
    </div>

</template>

<script>
import $ from 'jquery'

export default {
  name: 'MenuItemWrapper',
  data() {
    return {
      menuId: -1,
      show: true
    }
  },
  props: {
    paramId: {
      type: Number
    },
  },
  created() {
    this.menuId = this.paramId
  },
  computed: {
    collapseId() {
      return 'collapse_' + this.menuId
    }
  },
  mounted() {
    this.collapseMenu()
  },
  methods: {
    collapseMenu() {
      if (this.show) {
        $('#collapse_' + this.menuId).collapse('show')
      } else {
        $('#collapse_' + this.menuId).collapse('hide')
      }
      this.show = !this.show
    }
  }
}
</script>

<style scoped lang="sass">
@import "@/common/sass/base.sass"

.left-menu-item
  padding-left: $horizon
  padding-right: $horizon

.item-father
  background: $menuHeader
  padding: $horizonL $horizonXL $horizonS $horizonXL
  border-radius: $horizonS

.item-children
  padding-left: $horizon
  padding-right: $horizon

.item-children > div
  padding: 4.25px $horizonXL
  border-radius: $horizonS
  margin-top: $distance
  padding-left: $horizonXXL

</style>


