<template>
  <div class="modal fade" id="helpModal" data-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
    <div class="modal-dialog modal-lg modal-dialog-centered modal-dialog-scrollable">
      <div class="modal-content">
        <div class="modal-header">
          <h4 class="modal-title" id="helpModalTitle">
            帮助
          </h4>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body" id="helpModalBody">
          
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-light" @click="closeHelp">关闭</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import tool from '@/common/es/tool'

export default {
  name: 'HelpModal',
  methods: {
    closeHelp() {
      tool.closeModal('helpModal')
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


