<template>
<div>

  <!-- 顶部菜单 --> 
  <scaffold-bar class="position-fixed"></scaffold-bar>

  <div class="container-fluid">
    
    <div class="row wrapper">
      <!-- 左菜单 --> 
      <div class="col-sm-2 left-menu">
        <left-menu></left-menu>
        <left-sub-action></left-sub-action>
      </div>

      <!-- 右内容 -->
      <div class="col-sm-10 right-content">
        <home-content></home-content>
      </div>

    </div>

  </div>
  
  <!-- 提示 -->
  <toast></toast>
  <!-- 帮助 -->
  <help-modal></help-modal>
</div>
</template>

<script>
import LeftMenu from '@/components/Menu/LeftMenu'
import ScaffoldBar from '@/components/TopBar/ScaffoldBar'
import HomeContent from './Content/HomeContent.vue'
import Toast from '@/components/Dialog/Toast.vue'
import LeftSubAction from '@/components/Menu/LeftSubAction.vue'
import HelpModal from '@/components/Modal/Help/HelpModal.vue'

export default {
    name: 'HomeLayout',
    components: {
      LeftMenu,
      ScaffoldBar,
      HomeContent,
      Toast,
      LeftSubAction,
      HelpModal
    },
    created() {
      console.log('页面发生展示时调用。')
    }
}
</script>

<style scoped lang="sass">
@import "@/common/sass/base.sass"
.red
  background: rgb(255 ,174 ,200)

.container-fluid
  padding-left: 0
  padding-right: 0
  z-index: 80

.wrapper
  height: 100vh
  padding-top: $topBarH !important

.left-menu
  height: 100%
  padding-right: 0
  background: $scaffold
  z-index: 200
  position: relative
  
.right-content
  height: 100%
  padding-left: 0
  overflow-y: scroll

  
</style>


