<template>
  <div class="left-sub-action">
    <div class="lsa-item" @click="logOut">退出登录</div>
  </div>
</template>

<script>
export default {
  name: 'LeftSubAction',
  methods: {
    logOut() {
      this.$store.commit('loginOut', '')
      
      this.$router.push({
        path: '/login'
      })
    }
  }
}
</script>

<style scoped lang="sass">
@import "@/common/sass/base.sass"

.left-sub-action
  position: absolute
  margin-left: $horizon
  left: 0
  bottom: 0

.lsa-item
  padding: $horizonS $horizon
  color: $menuSub
</style>


